<h1 id="android-app-bar" tabindex="-1"><a class="header-anchor" href="#android-app-bar" aria-hidden="true">#</a> Android App Bar</h1>
<p>In Android 3 the <em>Title Bar</em> was changed to the <em>Action Bar</em> and got a bit more powerful. Then Material Design started to call it for <em>App bar</em>. Let's take a look at some of the things we can use it for.</p>
