<h2 id="the-title-bar" tabindex="-1"><a class="header-anchor" href="#the-title-bar" aria-hidden="true">#</a> The Title Bar</h2>
<p>When we first got Android, activities had a bar at the top called the <em>Title Bar</em>. As the name suggests, it was used to display the title of the activity (the value for the <code>label</code> attribute the <code>&lt;activity&gt;</code> has in the <code>AndroidManifest.xml</code> file, or (if that attribute is missing in the <code>&lt;activity&gt;</code>) the value for the <code>label</code> attribute the <code>&lt;application&gt;</code> element has).</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example showing what the Title Bar looked like on the first versions of Android.</p>
<CodeGroup>
<CodeGroupItem title="AndroidManifest.xml">
<div class="language-xml ext-xml line-numbers-mode"><pre v-pre class="language-xml"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>manifest</span> <span class="token attr-name">...</span><span class="token punctuation">></span></span>
	
	<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>application</span> <span class="token attr-name">...</span><span class="token punctuation">></span></span>
		
		<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>activity</span>
			<span class="token attr-name"><span class="token namespace">android:</span>name</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>.TestActivity<span class="token punctuation">"</span></span>
			<span class="token attr-name"><span class="token namespace">android:</span>label</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>This is the Title Bar<span class="token punctuation">"</span></span> <span class="token punctuation">/></span></span>
		
	<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>application</span><span class="token punctuation">></span></span>
	
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>manifest</span><span class="token punctuation">></span></span>
</code></pre><div class="highlight-lines"><br><br><br><br><br><br><div class="highlight-line">&nbsp;</div><br><br><br><br></div><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div></CodeGroupItem>
