<h2 id="the-options-menu" tabindex="-1"><a class="header-anchor" href="#the-options-menu" aria-hidden="true">#</a> The Options Menu</h2>
<p>An activity can contain a &quot;main&quot; menu, which is called the <em>Options Menu</em>. In the first versions of Android, an Android device was expected to contain a dedicated Menu hardware button (similar to the dedicated Back and Home hardware buttons), and when that button was clicked, the Options Menu is showed in front of the activity.</p>
<p>When the Options Menu should be shown (e.g. when the user clicks on the Menu hardware button), the method <code>Activity.onCreateOptionsMenu()</code> is called, and a <code>Menu</code> object is passed as an argument to it. That <code>Menu</code> object represents the Options Menu, and in this method you should populate the <code>Menu</code> object with the <code>MenuItem</code>s you want it to show. Then the GUI will visualize those <code>MenuItem</code>s on the screen.</p>
<div class="custom-container tip"><p class="custom-container-title">Populating the Menu</p>
<p>To populate a <code>Menu</code> with <code>MenuItems</code>, you can call various methods on the <code>Menu</code> object (e.g. <code>Menu.add()</code>), but most often you define the <code>MenuItem</code>s in an XML menu resource file in <code>res/menu/</code>, and then use a <code>MenuInflater</code> to populate the <code>Menu</code> based on the content in the menu resource file. See the example below for some of the details.</p>
</div>
<p>When the user clicks on one of the <code>MenuItem</code>s on the screen, the method <code>Activity.onOptionsItemSelected()</code> is called, and you receive the <code>MenuItem</code> the user clicked on as an argument, and you can react to the user's choice.</p>
<div class="custom-container tip"><p class="custom-container-title">More configurable!</p>
<p>Menus are more configurable than explained here. For example, each <code>MenuItem</code> can also have an icon that is shown instead of or in addition to the title of the <code>MenuItem</code>, etc.</p>
</div>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example showing how the Options Menu worked on the first versions of Android.</p>
<CodeGroup>
<CodeGroupItem title="AndroidManifest.xml">
<div class="language-xml ext-xml line-numbers-mode"><pre v-pre class="language-xml"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>manifest</span> <span class="token attr-name">...</span><span class="token punctuation">></span></span>
	
	<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>application</span> <span class="token attr-name">...</span><span class="token punctuation">></span></span>
		
		<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>activity</span>
			<span class="token attr-name"><span class="token namespace">android:</span>name</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>.TestActivity<span class="token punctuation">"</span></span>
			<span class="token attr-name"><span class="token namespace">android:</span>label</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>This is the Title Bar<span class="token punctuation">"</span></span> <span class="token punctuation">/></span></span>
		
	<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>application</span><span class="token punctuation">></span></span>
	
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>manifest</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div></CodeGroupItem>
<CodeGroupItem title="res/layout/activity_test.xml">
<div class="language-xml ext-xml line-numbers-mode"><pre v-pre class="language-xml"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>LinearLayout</span>
	<span class="token attr-name"><span class="token namespace">xmlns:</span>android</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>http://schemas.android.com/apk/res/android<span class="token punctuation">"</span></span>
	<span class="token attr-name"><span class="token namespace">android:</span>layout_width</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>match_parent<span class="token punctuation">"</span></span>
	<span class="token attr-name"><span class="token namespace">android:</span>layout_height</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>match_parent<span class="token punctuation">"</span></span>
	<span class="token attr-name"><span class="token namespace">android:</span>orientation</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>vertical<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
	
	<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>TextView</span>
		<span class="token attr-name"><span class="token namespace">android:</span>layout_width</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>wrap_content<span class="token punctuation">"</span></span>
		<span class="token attr-name"><span class="token namespace">android:</span>layout_height</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>wrap_content<span class="token punctuation">"</span></span>
		<span class="token attr-name"><span class="token namespace">android:</span>id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>@+id/the_text_view<span class="token punctuation">"</span></span>
		<span class="token attr-name"><span class="token namespace">android:</span>text</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>Hello!<span class="token punctuation">"</span></span><span class="token punctuation">/></span></span> 
	
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>LinearLayout</span><span class="token punctuation">></span></span>
</code></pre><div class="highlight-lines"><br><br><br><br><br><br><br><br><br><div class="highlight-line">&nbsp;</div><br><br><br></div><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div></CodeGroupItem>
<CodeGroupItem title="res/menu/activity_test.xml">
<div class="language-xml ext-xml line-numbers-mode"><pre v-pre class="language-xml"><code><span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>menu</span>
    <span class="token attr-name"><span class="token namespace">xmlns:</span>android</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>http://schemas.android.com/apk/res/android<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
    
    <span class="token comment">&lt;!-- Should use string resources for the title attribute! --></span>
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>item</span>
        <span class="token attr-name"><span class="token namespace">android:</span>title</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>The first choice<span class="token punctuation">"</span></span>
        <span class="token attr-name"><span class="token namespace">android:</span>id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>@+id/first_menu_item<span class="token punctuation">"</span></span> <span class="token punctuation">/></span></span>
    
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>item</span>
        <span class="token attr-name"><span class="token namespace">android:</span>title</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>The second choice<span class="token punctuation">"</span></span>
        <span class="token attr-name"><span class="token namespace">android:</span>id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>@+id/second_menu_item<span class="token punctuation">"</span></span> <span class="token punctuation">/></span></span>

<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>menu</span><span class="token punctuation">></span></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div></CodeGroupItem>
<CodeGroupItem title="TestActivity.kt">
<div class="language-kotlin ext-kt line-numbers-mode"><pre v-pre class="language-kotlin"><code><span class="token keyword">import</span> android<span class="token punctuation">.</span>app<span class="token punctuation">.</span>Activity
<span class="token keyword">import</span> android<span class="token punctuation">.</span>widget<span class="token punctuation">.</span>TextView

<span class="token keyword">class</span> TestActivity <span class="token operator">:</span> <span class="token function">Activity</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
	
    <span class="token keyword">lateinit</span> <span class="token keyword">var</span> theTextView <span class="token operator">:</span> TextView
    
	<span class="token keyword">override</span> <span class="token keyword">fun</span> <span class="token function">onCreate</span><span class="token punctuation">(</span>savedInstanceState<span class="token operator">:</span> Bundle<span class="token operator">?</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
		<span class="token keyword">super</span><span class="token punctuation">.</span><span class="token function">onCreate</span><span class="token punctuation">(</span>savedInstanceState<span class="token punctuation">)</span>
		
		<span class="token function">setContentView</span><span class="token punctuation">(</span>R<span class="token punctuation">.</span>layout<span class="token punctuation">.</span>activity_test<span class="token punctuation">)</span>
        
        theTextView <span class="token operator">=</span> <span class="token function">findViewById</span><span class="token punctuation">(</span>R<span class="token punctuation">.</span>id<span class="token punctuation">.</span>the_text_view<span class="token punctuation">)</span>
        
	<span class="token punctuation">}</span>
    
    <span class="token comment">// Called when the menu needs to be created.</span>
    <span class="token keyword">override</span> <span class="token keyword">fun</span> <span class="token function">onCreateOptionsMenu</span><span class="token punctuation">(</span>menu<span class="token operator">:</span> Menu<span class="token operator">?</span><span class="token punctuation">)</span><span class="token operator">:</span> Boolean <span class="token punctuation">{</span>
        
        <span class="token comment">// Add menu items from the menu XML file.</span>
        menuInflater<span class="token punctuation">.</span><span class="token function">inflate</span><span class="token punctuation">(</span>R<span class="token punctuation">.</span>menu<span class="token punctuation">.</span>activity_test<span class="token punctuation">,</span> menu<span class="token punctuation">)</span>
        
        <span class="token comment">// Add a menu item programmatically (should use string resources).</span>
        <span class="token keyword">val</span> menuItem <span class="token operator">=</span> menu<span class="token punctuation">.</span><span class="token function">add</span><span class="token punctuation">(</span><span class="token string">"The third choice"</span><span class="token punctuation">)</span>
        menuItem<span class="token punctuation">.</span><span class="token function">setOnMenuItemClickListener</span> <span class="token punctuation">{</span>
            theTextView<span class="token punctuation">.</span>text <span class="token operator">=</span> <span class="token string">"The third one"</span>
            <span class="token boolean">true</span> <span class="token comment">// Return true to indicate we handled the click.</span>
        <span class="token punctuation">}</span>
        
        <span class="token comment">// Let the class we inherit from add menu items if it wants.</span>
        <span class="token comment">// Returning true means the menu was successfully created.</span>
        <span class="token keyword">return</span> <span class="token keyword">super</span><span class="token punctuation">.</span><span class="token function">onCreateOptionsMenu</span><span class="token punctuation">(</span>menu<span class="token punctuation">)</span>
        
    <span class="token punctuation">}</span>
    
    <span class="token comment">// Called when the user has clicked on an Options Menu item.</span>
    <span class="token keyword">override</span> <span class="token keyword">fun</span> <span class="token function">onOptionsItemSelected</span><span class="token punctuation">(</span>item<span class="token operator">:</span> MenuItem<span class="token punctuation">)</span><span class="token operator">:</span> Boolean <span class="token punctuation">{</span>
        
        <span class="token comment">// Check the id of the Menu Item to figure out which</span>
        <span class="token comment">// it was.</span>
        <span class="token keyword">when</span><span class="token punctuation">(</span>item<span class="token punctuation">.</span>itemId<span class="token punctuation">)</span><span class="token punctuation">{</span>
            R<span class="token punctuation">.</span>id<span class="token punctuation">.</span>first_menu_item <span class="token operator">-></span> theTextView<span class="token punctuation">.</span>text <span class="token operator">=</span> <span class="token string">"The first one"</span>
            R<span class="token punctuation">.</span>id<span class="token punctuation">.</span>second_menu_item <span class="token operator">-></span> theTextView<span class="token punctuation">.</span>text <span class="token operator">=</span> <span class="token string">"The second one"</span>
            <span class="token comment">// If we can't handle the clicked menu item, let</span>
            <span class="token comment">// the activity we inherit from try and handle it.</span>
            <span class="token keyword">else</span> <span class="token operator">-></span> <span class="token keyword">return</span> <span class="token keyword">super</span><span class="token punctuation">.</span><span class="token function">onOptionsItemSelected</span><span class="token punctuation">(</span>item<span class="token punctuation">)</span>
        <span class="token punctuation">}</span>
        
        <span class="token comment">// Return true to indicate that we handled the click.</span>
        <span class="token keyword">return</span> <span class="token boolean">true</span>

    <span class="token punctuation">}</span>
    
	
<span class="token punctuation">}</span>
