<h2 id="the-action-bar" tabindex="-1"><a class="header-anchor" href="#the-action-bar" aria-hidden="true">#</a> The Action Bar</h2>
<p>In Android 3.0, when Android officially started to support tablets, the Title Bar was replaced with the <a href="https://developer.android.com/about/versions/android-3.0.html#action-bar" target="_blank" rel="noopener noreferrer">Action Bar<OutboundLink/></a>. The Action Bar can be seen as a more powerful version of the Title Bar, because it can not only display the title of the activity, but also contains additional features. To get a reference to the Action Bar, use <code>Activity.getActionBar()</code>.</p>
<p>To display the logo of the application in the Action Bar, use <code>theActionBar.setDisplayUseLogoEnabled(true)</code> (discouraged from Android 5 and on).</p>
<p>By default, the Action Bar contains the Options Menu items as a dropdown list. That menu is now called the <em>Overflow Menu</em>, but in the code we still refer to it as the Options Menu  (you don't need to do anything special to get the Overflow M; <code>Activity.onCreateOptionsMenu()</code> and <code>Activity.onOptionsItemSelected()</code> will be called when needed, just as before).</p>
<p>Each <code>MenuItem</code> can contain additional information about how it should be shown in the Action Bar/Overflow Menu. For example, by setting <code>showAsAction</code> to <code>ifRoom</code> on a <code>MenuItem</code>, that <code>MenuItem</code> will be shown directly in the Action Bar next to the dropdown list if the user's screen is wide enough.</p>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example showing how the Options Menu is shown on Android devices after Android 3.</p>
