<p>When clicking on a <code>MenuItem</code>, it's also possible to replace the main content in the Action Bar with another temporary widget. The typical example is the <code>SearchView</code>, which kind of is an <code>EditText</code> the user can type text into to search for something (you need to handle the search yourself). For an example of this, see the Android Developer training <a href="https://developer.android.com/training/search/setup" target="_blank" rel="noopener noreferrer">Setting Up the Search Interface<OutboundLink/></a>.</p>
<p>The Action Bar can also contain the tab titles, and clicking on them can change which fragment to show below the Action Bar. There are more things the Action Bar can be used for, read the documentation if you're curious.</p>
<p>Because the Action Bar was added in Android 3, applications running on older versions of Android won't have it. New features have also been added to it after Android 3, so even if your application runs on a version that contains the Action Bar, you can't use the latest features it contains, because they will only work on devices running the latest version of Android. Sight.</p>
<h3 id="the-app-bar" tabindex="-1"><a class="header-anchor" href="#the-app-bar" aria-hidden="true">#</a> The App Bar</h3>
<p>Material Design was released in Android 5, and they had a component named <em>App Bar</em>, which more or less served the same purpose as the Action Bar in Android. So from that time and on, the Action Bar is also often referred to as the App Bar, since that's the name it has in Material Design.</p>
<h3 id="the-toolbar" tabindex="-1"><a class="header-anchor" href="#the-toolbar" aria-hidden="true">#</a> The Toolbar</h3>
<p>In Android 5, they made it possible to use a view in the activity's GUI to function as the activity's Action Bar, and they created a dedicated view for this very purpose: the <code>Toolbar</code>. This way, it's possible to implement different type of bars to use as your Action Bar, and they are just as flexible as ordinary views in your GUI, so you can for example use them in animations (move them around, change their sizes, etc.).</p>
<p>If you want to use a <code>Toolbar</code> as you Action Bar in an activity, you:</p>
<ol>
<li>Tell the activity to not use its own default Action Bar. You do that in your Application/Activity theme by adding <code>&lt;item name=&quot;android:windowActionBar&quot;&gt;false&lt;/item&gt;</code> to the theme (or by inheriting from a theme ending with the name <code>.NoActionBar</code>, which already contains this item)</li>
<li>Add a <code>Toolbar</code> to your activity's GUI (i.e. in the XML layout file used by the activity)</li>
<li>Tell your activity that the <code>Toolbar</code> you have in the GUI should be used as the activity's Action Bar. You do that by calling the activity's method <code>setActionBar()</code> and pass it a reference to your <code>Toolbar</code></li>
</ol>
<p>However, to stay backward compatible, you use Android Jetpack implementations:</p>
<ol>
<li>Inherit from <code>AppCompatActivity</code> instead of <code>Activity</code></li>
<li>Use the <code>Toolbar</code> implementation from Android JetPack instead of the <code>Toolbar</code> implementation from Android</li>
<li>On the activity, call <code>setSupportActionBar()</code> instead of <code>setActionBar()</code></li>
</ol>
<div class="custom-container tip"><p class="custom-container-title">Example</p>
<p>Example showing an app working the same way as before, but now in a backward compatible way.</p>
<CodeGroup>
<CodeGroupItem title="AndroidManifest.xml">
<div class="language-xml ext-xml line-numbers-mode"><pre v-pre class="language-xml"><code><span class="token comment">&lt;!-- Same as before, but changed to a NoActionBar theme. --></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>manifest</span> <span class="token attr-name">...</span><span class="token punctuation">></span></span>
	
	<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>application</span> <span class="token attr-name">...</span><span class="token punctuation">></span></span>
		
		<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>activity</span>
			<span class="token attr-name"><span class="token namespace">android:</span>name</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>.TestActivity<span class="token punctuation">"</span></span>
			<span class="token attr-name"><span class="token namespace">android:</span>label</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>The Action Bar<span class="token punctuation">"</span></span>
            <span class="token attr-name"><span class="token namespace">android:</span>theme</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>Theme.MaterialComponents.DayNight.NoActionBar<span class="token punctuation">"</span></span> <span class="token punctuation">/></span></span>
		
	<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>application</span><span class="token punctuation">></span></span>
	
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>manifest</span><span class="token punctuation">></span></span>
</code></pre><div class="highlight-lines"><div class="highlight-line">&nbsp;</div><br><br><br><br><br><br><br><div class="highlight-line">&nbsp;</div><br><br><br><br></div><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br></div></div></CodeGroupItem>
<CodeGroupItem title="res/layout/activity_test.xml">
<div class="language-xml ext-xml line-numbers-mode"><pre v-pre class="language-xml"><code><span class="token comment">&lt;!-- Same as before, but added a Toolbar. --></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>LinearLayout</span>
	<span class="token attr-name"><span class="token namespace">xmlns:</span>android</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>http://schemas.android.com/apk/res/android<span class="token punctuation">"</span></span>
	<span class="token attr-name"><span class="token namespace">android:</span>layout_width</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>match_parent<span class="token punctuation">"</span></span>
	<span class="token attr-name"><span class="token namespace">android:</span>layout_height</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>match_parent<span class="token punctuation">"</span></span>
	<span class="token attr-name"><span class="token namespace">android:</span>orientation</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>vertical<span class="token punctuation">"</span></span><span class="token punctuation">></span></span>
    
    <span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>androidx.appcompat.widget.Toolbar</span>
        <span class="token attr-name"><span class="token namespace">android:</span>id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>@+id/toolbar<span class="token punctuation">"</span></span>
        <span class="token attr-name"><span class="token namespace">android:</span>layout_width</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>match_parent<span class="token punctuation">"</span></span>
        <span class="token attr-name"><span class="token namespace">android:</span>layout_height</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>?attr/actionBarSize<span class="token punctuation">"</span></span> <span class="token punctuation">/></span></span>
	
	<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>TextView</span>
		<span class="token attr-name"><span class="token namespace">android:</span>layout_width</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>wrap_content<span class="token punctuation">"</span></span>
		<span class="token attr-name"><span class="token namespace">android:</span>layout_height</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>wrap_content<span class="token punctuation">"</span></span>
		<span class="token attr-name"><span class="token namespace">android:</span>id</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>@+id/the_text_view<span class="token punctuation">"</span></span>
		<span class="token attr-name"><span class="token namespace">android:</span>text</span><span class="token attr-value"><span class="token punctuation attr-equals">=</span><span class="token punctuation">"</span>Hello!<span class="token punctuation">"</span></span><span class="token punctuation">/></span></span> 
	
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;/</span>LinearLayout</span><span class="token punctuation">></span></span>
</code></pre><div class="highlight-lines"><div class="highlight-line">&nbsp;</div><br><br><br><br><br><br><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><div class="highlight-line">&nbsp;</div><br><br><br><br><br><br><br><br></div><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br></div></div></CodeGroupItem>
