<div class="language-xml ext-xml line-numbers-mode"><pre v-pre class="language-xml"><code><span class="token comment">&lt;!-- Same as before. --></span>
<span class="token tag"><span class="token tag"><span class="token punctuation">&lt;</span>menu</span>
